  &.tsukasa {\n            font-family: \"Noto Serif JP\", serif;\n            font-size: 0.95em;\n        }\n    }\n}\n",":root {\n    // core\n    --os-text-font: normal normal normal 14px/1.3 \"Karla\";\n    --os-text-color: #4b545e;\n    --os-link-color: #294769;\n    --os-link-color__hover: #23558e;\n    --os-background-color: #eef0f3;\n    --os-background-color__hover: #e5e9ee;\n    --os-background-secondary-color: #dbdfe7;\n    --os-background-secondary-color__hover: #ced3dd;\n    --os-animation-duration: 0.2s;\n    --os-border-color: #c5ccd2;\n    --os-border: solid 1px var(--os-border-color);\n    --os-shadow: drop-shadow(0 1px 1.5px rgba(0, 0, 0, 0.05))\n        drop-shadow(0 1px 1px rgba(0, 0, 0, 0.06));\n\n    // toolbar\n    --os-toolbar-color: var(--os-background-secondary-color);\n    --os-toolbar-icon-color: #4b545e;\n\n    // extensions\n    // display\n    --os-bubble-color: #fbfbfe;\n    --os-bubble-background-color: var(--os-background-color);\n    --os-bubble-radius-large: 0.5em;\n    --os-bubble-radius-small: 0.1em;\n    --os-bubble-padding: 0.6em 0.85em;\n    --os-identifier-color: #5e6978;\n    --os-identifier-width: 70px;\n    --os-identifier-width-display: 30px;\n    --os-identifier-radius: 10px;\n}\n","/* tippy */\n\n.tippy-box[data-theme~=\"oissu\"] {\n    letter-spacing: 0px;\n\n    padding: 0.4em 0.3em;\n\n    background: var(--os-background-color);\n    border-radius: 6px;\n    border: var(--os-border);\n    color: var(--os-text-color);\n    filter: var(--os-shadow);\n    font: var(--os-text-font);\n    font-size: 0.8em;\n}\n\n.tippy-box[data-theme~=\"oissu-button\"] {\n    padding: 0;\n}\n\n.tippy-box[data-animation=\"shift-away\"][data-state=\"hidden\"] {\n    opacity: 0;\n}\n\n.tippy-box[data-animation=\"shift-away\"][data-state=\"hidden\"][data-placement^=\"top\"] {\n    transform: translateY(10px);\n}\n\n.tippy-box[data-animation=\"shift-away\"][data-state=\"hidden\"][data-placement^=\"bottom\"] {\n    transform: translateY(-10px);\n}\n\n.tippy-box[data-animation=\"shift-away\"][data-state=\"hidden\"][data-placement^=\"left\"] {\n    transform: translateX(10px);\n}\n\n.tippy-box[data-animation=\"shift-away\"][data-state=\"hidden\"][data-placement^=\"right\"] {\n    transform: translateX(-10px);\n}\n\n/* modernContext */\n\n.modern_context_js_outer {\n    z-index: 999999999999;\n    position: absolute;\n    padding: 0.5em;\n    display: none;\n    overflow: hidden;\n    transition: 0.3s cubic-bezier(0.5, 0, 0, 1);\n    cursor: default;\n    user-select: none;\n\n    letter-spacing: 0px;\n\n    background: var(--os-background-color);\n    border-radius: 6px;\n    border: var(--os-border);\n    color: var(--os-text-color);\n    filter: var(--os-shadow);\n    font: var(--os-text-font);\n    font-size: 0.8em;\n\n    .context_item {\n        width: 100%;\n        padding: 0.25em 0.75em;\n        box-sizing: border-box;\n        position: relative;\n        border-radius: 2.5px;\n\n        &:first-child {\n            pointer-events: none;\n\n            .context_item_inner {\n                text-transform: uppercase;\n                font-size: 0.8em;\n                font-weight: 700;\n                opacity: 0.5;\n            }\n        }\n\n        &:not(:first-child).hover {\n            background: var(--os-background-color__hover);\n        }\n\n        .context_item_inner {\n            transition: 0.1s;\n        }\n\n        &:active .context_item_inner {\n            transform: scale(0.95);\n        }\n\n        .feather {\n            font-size: 0.9em;\n            vertical-align: -0.1em;\n            margin-right: 0.25em;\n        }\n    }\n}\n\n.spinner {\n    position: absolute;\n    opacity: 0;\n    transition: var(--os-animation-duration) ease;\n}\n\n.oissu__embed-loading .spinner {\n    opacity: 1;\n    position: absolute;\n    z-index: 10;\n    left: 50%;\n    transform: translate(-50%, 160px);\n    width: 70px;\n\n    display: flex;\n    gap: 2.5px;\n    justify-content: center;\n}\n\n.spinner > div {\n    width: 14px;\n    height: 14px;\n    background-color: currentColor;\n\n    border-radius: 100%;\n    display: inline-block;\n    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;\n    animation: sk-bouncedelay 1.4s infinite ease-in-out both;\n}\n\n.spinner .bounce1 {\n    -webkit-animation-delay: -0.32s;\n    animation-delay: -0.32s;\n}\n\n.spinner .bounce2 {\n    -webkit-animation-delay: -0.16s;\n    animation-delay: -0.16s;\n}\n\n@-webkit-keyframes sk-bouncedelay {\n    0%,\n    80%,\n    100% {\n        -webkit-transform: scale(0.8);\n        opacity: 0.3;\n    }\n\n    40% {\n        -webkit-transform: scale(0.9);\n        opacity: 1;\n    }\n}\n\n@keyframes sk-bouncedelay {\n    0%,\n    80%,\n    100% {\n        -webkit-transform: scale(0.8);\n        transform: scale(0.8);\n        opacity: 0.3;\n    }\n\n    40% {\n        -webkit-transform: scale(0.9);\n        transform: scale(0.9);\n        opacity: 1;\n    }\n}\n",".oissu__embed {\n    margin: 10px auto 10px;\n    box-sizing: border-box;\n    background: var(--os-background-color);\n    border-radius: 6px;\n    border: var(--os-border);\n    overflow: hidden;\n    position: relative;\n    color: var(--os-text-color);\n    filter: var(--os-shadow);\n    min-height: 370px;\n\n    * {\n        transition: all var(--os-animation-duration) ease;\n    }\n\n    .oissu {\n        padding: 10px;\n        position: relative;\n        display: flex;\n        flex-direction: column;\n        gap: 0.75em;\n        font: var(--os-text-font);\n        letter-spacing: 0;\n        min-height: 250px;\n\n        p {\n            margin: 0;\n        }\n\n        a {\n            color: var(--os-link-color);\n\n            &:hover {\n                color: var(--os-link-color__hover);\n            }\n        }\n\n        .os-unit {\n            display: grid;\n            grid-template: auto / var(--os-identifier-width) 1fr;\n            column-gap: 10px;\n            row-gap: 2.5px;\n\n            .os-content {\n                grid-column: 2/3;\n\n                &.os-lines {\n                    display: flex;\n                    flex-direction: column;\n\n                    p {\n                        margin: 0px 0px 0.3em 0px;\n                        border-radius: var(--os-bubble-radius-small)\n                            var(--os-bubble-radius-large)\n                            var(--os-bubble-radius-large)\n                            var(--os-bubble-radius-small);\n                        align-self: flex-start;\n                        position: relative;\n\n                        &:last-child {\n                            margin: 0px;\n                            border-bottom-left-radius: var(\n                                --os-bubble-radius-large\n                            );\n                        }\n\n                        &:first-child {\n                            border-top-left-radius: 0;\n                        }\n                    }\n                }\n\n                &.os-rich {\n                    border-radius: 5px;\n                    overflow: hidden;\n                    flex: 0 0 auto;\n                    grid-column: 1/3;\n\n                    img {\n                        width: 100%;\n                        height: auto;\n                        filter: var(--os-shadow);\n                    }\n                }\n            }\n\n            span.os-identifier {\n                color: var(--os-identifier-color);\n                font-weight: 700;\n                text-align: right;\n            }\n\n            img.os-identifier {\n                grid-row: 1/3;\n                display: none;\n                border-radius: var(--os-identifier-radius);\n                background: var(--q-character-color);\n                width: var(--os-identifier-width-display);\n                margin-left: auto;\n            }\n\n            .os-content.os-lines {\n                p:hover {\